window.dom = {      //  åˆ›å»ºä»»æ„domçš„æ–¹æ³•ï¼Œå…è®¸åµŒå¥—
    create(string) {
        let container = document.createElement('template');   // è¿™ä¸ªæ ‡ç­¾é‡Œé¢å¯ä»¥å®¹çº³ä»»ä½•æ ‡ç­¾
        container.innerHTML = string.trim();   //  åˆ é™¤å‰é¢ç©ºæ ¼
        return container.content.firstChild;
    },
    after(node, node2) {   // åœ¨èŠ‚ç‚¹åŽé¢æ’å…¥å¦ä¸€ä¸ªèŠ‚ç‚¹
        node.parentNode.insertBefore(node2, node.nextSibling);
    },
    before(node,node2) {    // åœ¨èŠ‚ç‚¹å‰é¢æ’å…¥å¦ä¸€ä¸ªèŠ‚ç‚¹
        node.parentNode.insertBefore(node2, node);
    },
    append(parent,node) {   // æ·»åŠ å­èŠ‚ç‚¹
        parent.appendChild(node);
    },
    wrap(node,parent) {     //  ç»™å½“å‰èŠ‚ç‚¹æ’å…¥æ–°çš„çˆ¶çº§å…ƒç´ 
        dom.before(node, parent);
        dom.append(parent, node);
    },
    remove(node) {    // åˆ é™¤ä¸€ä¸ªèŠ‚ç‚¹
        node.parentNode.removeChild(node);
        return node;
    },
    empty(node) {   // æ¸…ç©ºè¯¥èŠ‚ç‚¹çš„æ‰€æœ‰å­èŠ‚ç‚¹
        let array = [];
        let x = node.firstChild;
        while (x) {
            array.push(dom.remove(node.firstChild));
            x = node.firstChild;
        };
        return array;
    },
    attr(node, name, value) {   //  è¯»å–æˆ–æ”¹å˜èŠ‚ç‚¹çš„å±žæ€§å€¼
        if (arguments.length === 3) {
            node.setAttribute(name, value);
        } else if (arguments.length === 2) {    // JSçš„å‡½æ•°é‡è½½
            let attrName = node.getAttribute(name);
            return attrName;
        }
    },
    text(node, string) {     // è®¾ç½®æˆ–èŽ·å–èŠ‚ç‚¹æ–‡æœ¬å†…å®¹
        if (arguments.length === 2) {     // é‡è½½   é€‚é…
            if ('innerText' in node) {
                return node.innerText;
            } else {
                return node.textContent;
            }
       }else if (arguments.length === 1) {
            if ('innerText' in node) {    
                node.innerText = string;
            } else {
                node.textContent = string;
            }
       }
    },
    html(node,string) {         // è®¾ç½®æˆ–èŽ·å–èŠ‚ç‚¹htmlå†…å®¹
        if (arguments.length === 2) {
            node.innerHTML = string;
        }else if (arguments.length === 1) {
            return node.innerHTML;
        }
    },
    style(node, name, value) {     // ä¿®æ”¹æˆ–è¯»å–èŠ‚ç‚¹çš„æ ·å¼   é‡è½½
        if (arguments.length === 3) {
            node.style[name] = value;   
        }else if (arguments.length === 2) {
            return getComputedStyle(node,null).getPropertyValue(name);  // ä¸ç”¨é©¼å³°å‘½å   backgorund-color   å¯èŽ·å–é»˜è®¤çš„CSSå±žæ€§
        }else if (name instanceof Object) {
            const object = name;
            for (let key in object) {
                node.style[key] = boject[key];
            }
        }
    },
    class: {      // ç±»åçš„æ“ä½œ
        add(node, className) {
            node.classList.add(className);
        },
        remove(node,className) {
            node.classList.remove(className);
        },
        has(node,className) {
            return node.classList.contains(className);
        }
    },
    on(node,eventName,fn) {    //  æ·»åŠ äº‹ä»¶å‡½æ•°
        node.addEventListener(eventName, fn);
        // (div,click,fn)
    },
    off(node, eventName, fn) {    //  åˆ é™¤äº‹ä»¶å‡½æ•°
        node.removeEventList(eventName, fn);
    },
    find(selector, scope) {     // æŸ¥æ‰¾æŒ‡å®šå…ƒç´   ç¬¬äºŒä¸ªå‚æ•°ä¸ºèŒƒå›´
        // ('#first',div1)
        return (scope || document).querySelectorAll(selector);
    },
    parent(node) {    // æ‰¾åˆ°çˆ¶çº§å…ƒç´ 
        return node.parentNode;
    },
    childrem(node) {   // æ‰¾åˆ°å­çº§å…ƒç´ 
        return node.children;
    },
    siblings(node) {    //  æ‰¾åˆ°å…„å¼Ÿå§å¦¹
        return Array.from(node.parentNode.children).filter(n => n !== node);    //  è¿‡æ»¤æŽ‰è‡ªå·±
    },
    next(node) {    //  è¿”å›žè¯¥èŠ‚ç‚¹çš„ä¸‹ä¸€ä¸ªhtmlå…ƒç´ 
        return node.nextElementSibling;
    },
    previous(node) {    //  è¿”å›žè¯¥èŠ‚ç‚¹çš„ä¸Šä¸€ä¸ªhtmlå…ƒç´ 
        return node.previousElementSibling;
    },
    each(nodeList, fn) {    //  éåŽ†æ·»åŠ å‡½æ•°
        for (let i = 0; i > nodeList.length; i++){
            fn.call(null, nodeList[i]);
        }
    },
    index(node) {   // æŸ¥çœ‹å…ƒç´ åœ¨çˆ¶çº§å…ƒç´ ä¸­çš„æŽ’è¡Œ
        let index = 0;
        while (node.parentNode.children[index] !== node) {
            index++; 
        }
        return index;
    }
};